<template>
  <div class="container">
    <h4 class="text-center mt-5"></h4>
    <div class="input-group mb-3">
      <!-- store  -->
      <input v-model="$store.state.board.searchWord" @keydown.enter="boardList" type="text" class="form-control" />
      <button @click="boardList" class="btn btn-success" type="button">Search</button>
      <!-- enter   search  -->
    </div>

    <table class="table table-hover">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <!-- store  -->

        <!--  store   . -->
        <!-- <tr v-for="(board, index) in $store.state.board.list" @click="boardDetail(board.boardId)" v-bind:key="index"> -->

        <!-- getters    -->
        <!-- computed - listGetters - getBoardList  -->
        <tr style="cursor: pointer" v-for="(board, index) in listGetters" @click="boardDetail(board.boardId)"
          v-bind:key="index">
          <!--       -->
          <td>{{ board.boardId }}</td>
          <td>{{ board.title }}</td>
          <td>{{ board.userName }}</td>
          <td>
            {{
              makeDateStr(board.regDt.date.year, board.regDt.date.month, board.regDt.date.day, ".")
            }}
          </td>
          <td>{{ board.readCount }}</td>
        </tr>
      </tbody>
    </table>
    <button v-show="position" class="btn btn-sm btn-outline-success" @click="showInsertModal"></button>

    <!-- props  X -->
    <!-- <pagination 
      v-bind:listRowCount="listRowCount"
      v-bind:pageLinkCount="pageLinkCount"
      v-bind:currentPageIndex="currentPageIndex"
      v-bind:totalListItemCount="totalListItemCount"
      v-on:call-parent="movePage"
    ></pagination> -->
    <pagination v-on:call-parent="movePage"></pagination>
    <!-- v-on:call-parent="@"  @   
                                             this.$emit("@") -->

    <insert-modal v-on:call-parent-insert="closeAfterInsert"></insert-modal>
    <!-- props  -->
    <detail-modal v-on:call-parent-change-to-update="changeToUpdate" v-on:call-parent-change-to-delete="changeToDelete">
    </detail-modal>
    <update-modal v-on:call-parent-update="closeAfterUpdate"></update-modal>
  </div>
</template>

<script>
  import InsertModal from "./modals/InsertModal.vue";
  import DetailModal from "./modals/DetailModal.vue";
  import UpdateModal from "./modals/UpdateModal.vue";

  import {
    Modal
  } from "bootstrap";

  import http from "@/common/axios.js";
  import util from "@/common/util.js";

  import Pagination from "./Pagination.vue";

  // 
  import Vue from "vue";
  import VueAlertify from "vue-alertify";
  Vue.use(VueAlertify);

  export default {
    name: "BoardMain",
    components: {
      InsertModal,
      DetailModal,
      UpdateModal,
      Pagination,
    },
    data() {
      return {
        // modal
        insertModal: null,
        detailModal: null,
        updateModal: null,
        position: false,
      };
    },
    computed: {
      // gttters 
      listGetters() {
        return this.$store.getters.getBoardList; // no getBoardList()
      },
    },
    methods: {
      // list
      // store actions  
      //    
      boardList() {
        this.$store.dispatch("boardList");
  
        console.log("position : " + this.$store.state.user.userPosition);
        if (this.$store.state.user.userPosition == 1) {
          this.position = true
        }
      },

      // pagination
      movePage(pageIndex) {
        console.log("BoardMainVue : movePage : pageIndex : " + pageIndex);

        // store commit  
        // this.offset = (pageIndex - 1) * this.listRowCount;
        // this.currentPageIndex = pageIndex;
        this.$store.commit("SET_BOARD_MOVE_PAGE", pageIndex);

        this.boardList();
      },

      // util
      makeDateStr: util.makeDateStr,

      // insert
      showInsertModal() {
        this.insertModal.show();
      },

      closeAfterInsert() {
        this.insertModal.hide();
        this.boardList();
      },

      // detail
      // board-vue    
      // Detail Component  Server       Detail 
      boardDetail(boardId) {
        // store 
        // this.boardId = boardId;
        // this.$store.commit('mutateSetBoardBoardId', boardId);

        http
          .get(
            "/boards/" + boardId // props variable
          )
          .then(({
            data
          }) => {
            console.log("DetailModalVue: data : ");
            console.log(data);

            if (data.result == "login") {
              this.$router.push("/login");
            } else {
              this.$store.commit("SET_BOARD_DETAIL", {
                boardId: data.dto.boardId,
                title: data.dto.title,
                content: data.dto.content,
                userName: data.dto.userName,
                regDt: this.makeDateStr(
                  data.dto.regDt.date.year,
                  data.dto.regDt.date.month,
                  data.dto.regDt.date.day,
                  "."
                ),
                fileList: data.dto.fileList,
                sameUser: data.dto.sameUser, // not data.dto.sameUser
              });

              this.detailModal.show();
            }
          })
          .catch((error) => {
            console.log("DetailModalVue: error ");
            console.log(error);
          });
      },

      // update
      // Detail  board data   
      // changeToUpdate( board ){
      //   this.board = board;
      //   this.detailModal.hide();
      //   this.updateModal.show();
      // },
      changeToUpdate() {
        // board parameter  .
        // data update  X
        // this.board = board;
        this.detailModal.hide();
        this.updateModal.show();
      },

      closeAfterUpdate() {
        // boardId   X watch  X
        //this.boardId = 0;

        this.updateModal.hide();
        this.boardList();
      },

      // delete
      // $emit board  X
      // changeToDelete(board){
      changeToDelete() {
        this.detailModal.hide();

        var $this = this; // alertify.confirm-function() this  alertify 
        this.$alertify.confirm(
          //' ', '  ?', <- ???? title  cancel ok  
          "  ?",
          function () {
            // board.boardId  X
            $this.boardDelete(); // $this 
          },
          function () {
            console.log("cancel");
          }
        );
      },
      boardDelete() {
        // parameter  X
        console.log(">>>>>>>>>>>>>>>>>>>>> " + this.$store.state.board.boardId);
        http
          .delete("/boards/" + this.$store.state.board.boardId)
          .then(({
            data
          }) => {
            console.log("BoardMainVue: data : ");
            console.log(data);
            if (data.result == "login") {
              this.$router.push("/login");
            } else {
              this.boardList();
            }
          })
          .catch((error) => {
            console.log(error);
          });
      },
    },
    created() {
      this.boardList();
    },
    mounted() {
      this.insertModal = new Modal(document.getElementById("insertModal"));
      this.detailModal = new Modal(document.getElementById("detailModal"));
      this.updateModal = new Modal(document.getElementById("updateModal"));
    },
  };
</script>

<style scoped></style>
