<template>
  <div class="container">
    <section
      class="
        section
        register
        min-vh-75
        d-flex
        flex-column
        align-items-center
        justify-content-center
        py-4
      "
    >
      <div class="container">
        <div class="row justify-content-center">
          <div
            class="col-lg-4 col-md-6 d-flex flex-column align-items-center justify-content-center"
          >
            <div class="d-flex justify-content-center py-4">
              <a href="index.html" class="d-flex align-items-center">
                <i class="bi bi-house" style="font-size: 1.5rem">&nbsp;</i>
                <span class="d-none d-lg-block" style="font-size: 1.7rem">HappyHouse</span>
              </a>
            </div>
            <!-- End Logo -->

            <div class="card mb-3">
              <div class="card-body">
                <div class="pt-4 pb-2">
                  <h5 class="card-title text-center pb-0 fs-4">Login to Your Account</h5>
                  <p class="text-center small">Enter your ID & password to login</p>
                </div>

                <div class="row g-3 needs-validation" novalidate>
                  <div class="col-12">
                    <label for="Id" class="form-label">ID</label>
                    <div class="input-group has-validation">
                      <input type="text" v-model="$store.state.login.userId" class="form-control" />
                      <div class="invalid-feedback">Please enter your Id.</div>
                    </div>
                  </div>

                  <div class="col-12">
                    <label for="yourPassword" class="form-label">Password</label>

                    <input
                      type="password"
                      class="form-control"
                      v-model="$store.state.login.userPassword"
                    />
                    <div class="invalid-feedback">Please enter your password!</div>
                  </div>

                  <!-- <div class="col-12">
                    <div class="form-check">
                      <input
                        class="form-check-input"
                        type="checkbox"
                        name="remember"
                        value="true"
                        id="rememberMe"
                      />
                      <label class="form-check-label" for="rememberMe">Remember me</label>
                    </div>
                  </div> -->
                  <div class="col-12 mt-4">
                    <button @click="login" class="btn btn-success w-100">Login</button>
                  </div>
                  <div>
                    <p class="small mb-0">Don't have account?</p>
                    <div class="row">
                      <div class="col-5"></div>
                      <div class="col">
                        <router-link to="/register" class=""> Create an account</router-link>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div class="credits">
              <!-- All the links in the footer should remain intact. -->
