    </section>
  </div>
</template>

<script>
import Vue from "vue";
import VueAlertify from "vue-alertify";

Vue.use(VueAlertify);
import http from "@/common/axios.js";

export default {
  name: "Login",

  methods: {
    login() {
      http
        .post("/login", {
          userId: this.$store.state.login.userId,
          userPassword: this.$store.state.login.userPassword,
        })
        .then(({ data }) => {
          console.log("LoginVue: data : ");
          console.log(data);

          this.$store.commit("SET_LOGIN", {
            isLogin: true,
            userNo: data.userNo,
            userName: data.userName,
            userProfileImageUrl: data.userProfileImageUrl,
            userId: data.userId,
            userPhone: data.userPhone,
            userEmail: data.userEmail,
            userPosition: data.userPosition,
            userPassword: data.userPassword,
          });

          //== <route-link :to=path>
          console.log("position : "+this.$store.state.user.userPosition);
          this.$router.push("/main");
        })
        .catch((error) => {
          console.log("LoginVue: error : ");
          console.log(error);
          if (error.response.status == "404") {
            this.$alertify.error("   .");
          } else {
            this.$alertify.error("Opps!!   .");
          }
        });
    },
  },
};
//https://joshua1988.github.io/web-development/vuejs/vuex-getters-mutations/ 
// getter
//       /
// Vuex   (state)      ,
// Vuex        ,
