// Actions    

// state        =>  ,  
// mutation   -> commit
// this.$store.mutations.addCounter;(X)
// this.$store.commit('addCounter');(O)
// this.$store.commit('addCounter', 10);(O)
// this.$store.commit('addCounter', {
//   value: 10,
//   arr: ["a", "b", "c"]
// });(O)

// action  -> dispatch
